body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  display: grid;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.Question-Wrapper {
  padding: 100px;
}

.Sidebar-Wrapper {
  padding: 25px;
}

#sidebarsteve {
  width: 25%;
  height: 100%;
  float: left;

  border-right: 5px solid gray;
}

#question-title {
  font-family: "Lucida Grande", "Lucida Sans Unicode", "Lucida Sans",
    "DejaVu Sans", Verdana, "sans-serif";
  font-size: 24px;
  border-bottom: 2px solid blue;
  padding: 5px 45px 5px 5px;
  margin-left: 0px;
  display: inline;
  float: left;
}
body {
  height: 100%;
}
.topnav {
  overflow: hidden;
  background-color: #e9e9e9;
  border: 2px solid blue;
  border-radius: 4px;
  width: 300px;
  display: block;
  margin: auto;
}
.topnav a {
  float: right;
  width: 10%;
  display: block;
  color: blue;
  text-align: center;
  padding: 4px 6px;
  text-decoration: none;
  font-size: 24px;
}
.topnav a:hover {
  background-color: green;
}
.topnav input[type="text"] {
  width: 75%;
  float: left;
  padding: 6px;
  border: none;
  font-size: 17px;
  color: black;
  background-color: #e9e9e9;
}

.button {
  background-color: #4caf50;
  border: none;
  color: white;
  padding: 15px 25px;
  text-align: center;
  margin: auto;
  display: block;
  font-size: 16px;
  cursor: pointer;
  border-radius: 5px;
}
.question {
  display: inline-block;
  position: static;
  border: 1px solid black;
  border-radius: 5px;
  width: 95%;
  /* margin: 0px auto; */
  margin-top: 25px;
}
.upvote {
  float: left;
  color: blue;
  background-color: green;
  border: none;
  margin: auto;
  display: block;
}
.columnA {
  width: 40px;
  /*float: left;*/
  border-right: 1px solid black;
  text-align: center;
}
.columnB {
  -webkit-flex: 1 1;
          flex: 1 1;
  margin: 5px 10px 5px 10px;
  font-family: Arial;
}

.row {
  display: -webkit-flex;
  display: flex;
  width: 100%;
  height: 80px;
}

/* Hover effect on row for feedback */
.row:hover {
  background-color: #dfe3ee;
}

.row:after {
  content: "";
  display: table;
  clear: both;
}

#cardHeader {
    text-align: left;
}
